import axios from "axios";

export const namespace = "eth_address";
const initialState = {
    items: [],
    rows:""
};

export default function defaultReducer(
    state = initialState,
    action
) {
    switch (action.type) {
        case namespace + "/setState":
            return {
                ...state,
                ...action.payload
            };
        default:
            return state;
    }
}


export function fetchRows(callback) {
    return dispatch => {
        const loading = window.weui.loading("...");
        return axios.get(`/payment/eth/addresses`)
            .then(({data}) => {
                loading.hide();
                callback && callback(data)
                dispatch({
                    type: namespace + "/setState",
                    payload: {
                        items: data.body.rows
                    }
                });
            })
            .catch(error => {
                loading.hide();
                callback && callback({error})
            });
    };
}


export function saveRow({rows}, callback) {
    debugger
    return dispatch => {
        const loading = window.weui.loading("...");
        return axios.post(`/payment/eth/address`, {
            value:rows
        })
            .then(({data}) => {
                loading.hide();
                window.weui.toast("");
                dispatch({
                    type: namespace + "/setState",
                    payload: {
                        items: data.body.rows
                    }
                });
                callback && callback(data)
            })
            .catch(error => {
                loading.hide();
                callback && callback({error})
            });
    };
}


export function delRow(key, callback) {
    return dispatch => {
        const loading = window.weui.loading("...");
        return axios.delete(`/payment/eth/address/${key}`)
            .then(({data}) => {
                loading.hide();
                window.weui.toast("");
                dispatch({
                    type: namespace + "/setState",
                    payload: {
                        items: data.body.rows
                    }
                });
                callback && callback(data)
            })
            .catch(error => {
                loading.hide();
                callback && callback({error})
            });
    };
}

